  font-family: 'Source Code Pro', monospace;

}
.flex {
  display: flex;
  /* align-items: center; */
  justify-content: center;

}
.topp {
  padding-top: 300px;
}
.timer-container {
  margin: -50px 20px 20px;
}
.actions-wraper span {
  font-size: 40px;
  margin: 0 10px;
}
.clock-container {
  padding: 10px;
  border: 2px solid #F8F8F8;
  border-radius: 10px;
  padding: 10px;
  margin-top: 20px;
  width: 400px;
  display: inline-block;
}
.clock-container span {
  font-size: 60px;
}
.clock-container h1 {
  margin: 0;
}
.timer-container h2 {
  margin: 20px;
}


.btn {
  background-color: #222;
  border: 1px solid #563d7c;
  color: #F8F8F8;
  font-weight: bolder;
  cursor: pointer;

}
.btn:hover {
  background-color: #222;
  border: 1px solid #222;
  color: green;
}
.btn:active {
  background-color: #222;
  border: 1px solid #222;
  color: green;
}

