.btn {
  background-color: #222;
  border: 1px solid #563d7c;
  color: #F8F8F8;
  font-weight: bolder;

}
.btn:hover {
  background-color: #222;
  border: 1px solid #222;
  color: green;
}
.btn:active {
  background-color: #222;
  border: 1px solid #222;
  color: green;
}

.actions-wrapper button {
  font-size: 20px;
  margin: 10px;
  padding: 10px 20px;

}

.actions-wrapper span {
  font-size: 40px;
  margin: 0 10px;
  width: 50px;
}
